$product-item-class: '.product-cat' !default;
$product-item-detail-class: '.product-detail' !default;

$small-line-height: $font-size-base * $line-height-base * $small-font-size/100%;

#{$product-item-detail-class} {

	.fancybox-content & {
		max-width: 100%;
		width: 992px;
	}

	.l-main__title {
		margin-bottom: map-get($spacers, 6) !important;
	}

	&-slider-container {

		@supports (position: sticky) {
			position: sticky;
			top: $compact-height + map-get($spacers, 5);;
			z-index: $zindex-sticky;
		}

		@include media-breakpoint-up(md) {
			margin-bottom: map-get($spacers, 4);
		}
	}

	&-slider-block {
		position: relative;
		margin-bottom: map-get($spacers, 3);

		@include media-breakpoint-up(md) {
			margin-bottom: map-get($spacers, 4);
		}
		
		
		#{$product-item-class}-deals {
			left: 0;
			top: 0;
		}
		
		#{$product-item-class}-image-action {
			display: block;
		}
	}

	&-slider-images-container {
		// min-height: 300px;
		overflow: hidden;
		// white-space: nowrap;

		&.owl-loaded {
			overflow: visible;
		}
	}

	&-slider-images-container &-slider-image {
		@extend %vcenter; /* TODO need set height */

		display: block;
		// min-height: 300px;
		height: 300px;
		text-align: center;

		> img {
			@extend %vcenter__in;

			max-height: 300px;
			max-width: 100%;
			width: auto;


		}

		@include media-breakpoint-up(md) {
			height: 450px;
			> img {
				max-height: 450px;
			}
		}
	}

	&-slider-images-container.owl-loaded &-slider-image {
		// display: block;
		cursor: pointer;
	}

	&-slider-dots {
		

		> .slider-dots {
			display: block;
			text-align: center;
		}

		.owl-preview {
			display: none;
		}

		@include media-breakpoint-up(md) {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			height: auto;
			margin: auto auto map-get($spacers, 4);
			max-width: 336px;
			max-width: 21rem;

			&:after {
				content: '';
				flex: auto;
				max-width: 5.75rem;
				width: 33.33333%;
			}

			&.slider-dots {
				display: flex;
			}

			.owl-dot {
				background: transparent;
				border: 2px solid transparent;
				border-radius: 0;
				display: inline-block;
				height: auto;
				margin: map-get($spacers, 3);
				padding: map-get($spacers, 1);
				position: relative;
				width: auto;

				&:focus,
				&:active {
					border: 2px solid transparent;
				}

				&.active {
					background: transparent;
					border: 2px solid theme-color('primary');
					width: auto;
				}
			}

			.owl-preview {
				background-color: transparent;
				background-repeat: no-repeat;
				background-position: center center;
				border: none;
				cursor: pointer;
				display: block;
				height: 4.375rem;
				margin: auto;
				outline: 0;
				width: 4.375rem;
			}
		}
	}

	// prices
	&-price-current#{$product-item-class}-price-current {
		font-size: $h4-font-size;
	}

	&-price-old#{$product-item-class}-price-old {
		font-size: $h5-font-size;
	}

	&-price-economy#{$product-item-class}-price-economy {
		font-size: $font-size-sm;
	}

	// properties
	&-properties {
		font-size: $font-size-sm;
	}

	&-properties #{$product-item-class}-properties dd {
		&:after {
			height: map-get($spacers, 3);
		}
		
		&:last-of-type:after {
			height: 0;
		}
	}

	// price ranges
	& &-price-ranges {
		@include clearfix();

		margin-bottom: 0;

		> dt {
			color: $extra;
			font-weight: normal;
		}

		> dt,
		> dd,
		> dt:last-of-type,
		> dd:last-of-type {
			margin-bottom: map-get($spacers, 2);
			margin-top: map-get($spacers, 2);
		}
	}

	// sale
	&-sale {
		align-items: center;
		background-color: $price-economy-bg;
		border-radius: 1.5rem;
		display: flex;
		font-size: $font-size-sm;
		margin: 0 0 map-get($spacers, 3);
		padding: map-get($spacers, 3) map-get($spacers, 4) map-get($spacers, 3) map-get($spacers, 3);


		&:last-child {
			margin-bottom: 0;
		}
	}

	&-sale-title {
		display: block;
	}

	&-sale-icon {
		border-radius: 50%;
		border-radius: 50%;
		display: inline-block;
		height: 2rem;
		margin-right: map-get($spacers, 3);
		vertical-align: middle;
		// width: 1.75rem;
	}

	@include media-breakpoint-up(md) {

		&-sale-title {
			display: inline;
		}

		&-sale-body {
			@include text-truncate;

			white-space: nowrap;
		}
	}

	// share
	&-share {
		margin-bottom: map-get($spacers, 3);
		// padding-top: map-get($spacers, 4);
		text-align: center;

		@include media-breakpoint-up(md) {
			margin-bottom: map-get($spacers, 4);
		}
	}

	&-info-container {
		border-bottom: 1px solid $compact;
		margin-bottom: map-get($spacers, 6);
		padding-bottom: map-get($spacers, 4);
		padding-top: map-get($spacers, 4);

		@include media-breakpoint-up(md) {
			border-top: 1px solid $compact;
			margin-bottom: map-get($spacers, 4);
		}
	}

	&-size-table-icon {
		font-size: 1.33334em;
	}

	// print
	
	.print-link {
		position: relative;

		&:before {
			position: absolute;
      		width: 15px;
      		height: 15px;
      		top: 2px;
			left: -20px;
			content: url(data:image/svg+xml;base64,);

		}
	}
}
